import DescribePrompt from './DescribePrompt';
import DescribePost from './DescribePost';
import DescribeHold from './DescribeHold';
import { sanitize } from '../Util';

function renderSwitch(subState, game, player, error, description, updateDescription, addDescription) {
    switch (subState) {
        case 'prompt':
            return <DescribePrompt game={game} player={player} error={error} description={description} updateDescription={updateDescription} addDescription={addDescription} />
        case 'hold':
            return <DescribeHold game={game} player={player} error={error} />
        case 'post':
            return <DescribePost game={game} player={player} error={error} />
        default:
            return <div></div>
    }
}

function Describe({ game, player, error, description, setDescription, addDescription }) {
    function updateDescription(newValue) {
        const allowedDescription = newValue.split('').filter(letter => !player.prompt.word.toLowerCase().includes(letter.toLowerCase())).join('');
        setDescription(sanitize(allowedDescription, 16));
    }
    return (
        <div className='centralCol' style={{ 'textAlign': 'left' }}>
            {renderSwitch(game.subState, game, player, error, description, updateDescription, addDescription)}
        </div>
    );
}

export default Describe;
