import { sanitize } from '../Util';
import ScoreTable from './ScoreTable';

function Final({ game, player, resetGame }) {
    return (
        <div className='centralCol' style={{ 'textAlign': 'left' }}>
            <h1>Final Scores</h1>
            <ScoreTable game={game} isFinal={true} />
            <div style={{ "marginTop": "4vh" }}>
                {
                    player.isHost
                        ?
                        <input type="button" value="Play Again" onClick={e => resetGame()} />
                        :
                        <p>To play again, ask the host to click "Play Again"</p>
                }   
            </div>
        </div>
    );
}

export default Final;
