import { React } from 'react';

function ScoreTable({ game, isFinal = false }) {
    const players = game.players.map((p, i) => {return {...p, justDescribed: i === game.descriptionIndex}});
    return (
        <table style={{ 'fontSize': '1.1em', 'width': '100%', 'borderCollapse': 'collapse' }}>
            <thead >
                <tr >
                    <th style={{ 'paddingRight': '30px', 'borderBottom': 'solid 1px grey' }}>Player</th>
                    <th style={{ 'paddingRight': '30px', 'borderBottom': 'solid 1px grey' }}>{isFinal ? '' : 'Guess'}</th>
                    <th style={{ 'textAlign': 'center', 'borderBottom': 'solid 1px grey' }}>Score</th>
                </tr>
            </thead>
            <tbody>
                {
                    players.sort((p1, p2) => {return p2.score - p1.score}).map((p, i) => {
                        return (
                            <tr key={i}>
                                <td>{(p.score === Math.max(...(game.players.map(player => player.score))) && (isFinal)) ? 'üèÜ' : ''} {p.name}</td>
                                {
                                    isFinal ? <td></td> : <td>
                                        <span style={{ 'fontWeight': 'bold', 'paddingRight': '2px' }}>
                                            {
                                                (p.guess !== null)
                                                    ?
                                                    ((p.guess.toLowerCase() === game.wordToGuess.toLowerCase())
                                                        ?
                                                        <span style={{ 'color': 'lightgreen' }}>‚úì </span>
                                                        :
                                                        <span style={{ 'color': 'tomato' }}>‚úó </span>
                                                    )
                                                    :
                                                    ''
                                            }
                                        </span>
                                        {p.justDescribed ? '-' : (p.guess === null ? 'Ran out of time' : p.guess)}
                                    </td>
                                }

                                <td style={{ 'textAlign': 'center' }}>{p.score}</td>
                            </tr>
                        )
                    })
                }
            </tbody>
        </table>
    )
}

export default ScoreTable;