
function Lobby({ game, player, beginGame, error }) {
    return (
        <div className='centralCol' style={{ 'textAlign': 'left', 'paddingLeft': '10px' }}>
            <h1 style={{ 'textAlign': 'left', 'marginTop': '3vh' }}>Lobby</h1>
            <p>Invite players using the code: <span style={{ 'fontWeight': 'bold' }}>{game.id}</span></p>

            {((player !== null) && ('isHost' in player)) ?
                ((player.isHost) ?
                    <div>
                        <input type='button' onClick={e => beginGame()} value='Start Game' />
                        <div style={{ 'height': '1em' }}>
                            <p className='error'>{error}</p>
                        </div>
                        <p>You are the host, only you can start the game</p>
                    </div>
                    : <p>Waiting for the host to start the game</p>)
                : <div></div>}


            <h2 style={{ 'textAlign': 'left', 'marginTop': '3vh' }}>Players</h2>

            {('players' in game) ? game.players.map((p, i) => {
                return (
                    <p key={i} style={{ 'margin': '10px 0', 'fontSize': 'large', 'fontWeight': 'bold' }}>- {p.name}</p>
                )
            }) : <div></div>
            }
        </div>
    );
}

export default Lobby;
