import Timer from '../Timer';

function DescribePrompt({ game, player, error, description, updateDescription, addDescription }) {

    return (
        <div>
            <p style={{ 'fontSize': '1.5em' }}>
                Describe this {player.prompt.cat}: <br />
                <span style={{ 'fontSize': '2em', 'fontWeight': 'bold' }}>
                    {player.prompt.word}
                </span>
            </p>
            <form onSubmit={e => { e.preventDefault(); addDescription() }}>
                <div className='labelledInputContainer' style={{ 'display': 'inline-block' }}>
                    <div className='labelledInputLabelContainer'>
                        <label htmlFor='addDescription' >Your Description</label>
                    </div>
                    <input className={'labelledInputInput'} name='addDescription' type='text' value={description} onChange={e => updateDescription(e.target.value)} />
                </div>
                <div className='labelledInputContainer' style={{ 'display': 'inline-block', 'marginLeft': '10px' }}>
                    <input type='submit' value='Send' />
                </div>
            </form>
            <div style={{ 'height': '1em' }}>
                <p className='error'>{error}</p>
            </div>
            <div style={{ 'textAlign': 'center', 'marginTop': '2vh', 'height': '100px' }}>
                <Timer endTime={game.latestTimeoutEnd} startTime={game.latestTimeoutStart}></Timer>
            </div>
            <div style={{ 'marginTop': '2vh', 'backgroundColor': '#111', 'padding': '5px 10px', 'borderRadius': '5px' }}>
                <h3 style={{ 'margin': 0 }}>Remember</h3>
                <ul style={{ 'paddingLeft': '15px' }}>
                    <li style={{ 'padding': 0 }}>Use 1 to 16 characters</li>
                    <li style={{ 'padding': 0 }}>Do not use letters in the word <span style={{ 'fontWeight': 'bold' }}>{player.prompt.word}</span></li>
                    <li style={{ 'padding': 0 }}>Guessers will be told the category</li>
                </ul>
            </div>

        </div>
    );
}

export default DescribePrompt;
