import React from 'react';

class Timer extends React.Component {
    constructor(props) {
        super();
        const totalTimerDuration = Math.round((props.endTime - props.startTime) / 1000)
        const remainingTimerDuration = Math.round((props.endTime - Date.now()) / 1000);
        this.state = { time: {}, seconds: remainingTimerDuration, totalTimerDuration: totalTimerDuration };
        this.timer = 0;
        this.startTimer = this.startTimer.bind(this);
        this.countDown = this.countDown.bind(this);
    }

    componentDidMount() {
        this.startTimer();
    }

    componentWillUnmount() {
        clearInterval(this.timer);
    }

    startTimer() {
        if (this.timer === 0 && this.state.seconds > 0) {
            this.timer = setInterval(this.countDown, 100);
        }
    }

    countDown() {
        // Remove one second, set state so a re-render happens.
        let seconds = this.state.seconds - .1;
        this.setState({
            seconds: seconds,
        });

        // Check if we're at zero.
        if (seconds <= 0) {
            clearInterval(this.timer);
        }
    }

    render() {
        const proportion = (this.state.totalTimerDuration - this.state.seconds) / this.state.totalTimerDuration;
        const angle = (2 * Math.PI * proportion) - Math.PI / 2;

        let path = 'M 50 50 L 50 0 L 100 0 ';
        if (proportion > .25) {
            path += 'L 100 100 ';
        }
        if (proportion > .5) {
            path += 'L 0 100 ';
        }
        if (proportion > .75) {
            path += 'L 0 0 ';
        }
        path += ' L ' + (50 + 50 * Math.cos(angle)) + ' ' + (50 + 50 * Math.sin(angle)) + ' ';
        path += ' L 50 50';

        return (
            <div style={{ 'display': 'inline-block', 'height': '100px', 'width': '100px' }}>
                <svg height='100px' width='100px'>
                    <rect width='100' height='100' fill='#222' />
                    <circle cx='50' cy='50' r='50' fill={'#' + (10 + Math.round(proportion * 89)) + '' + (10 + Math.round((1-proportion) * 89)) + '11'} />
                    <path d={path} fill='#222' />
                </svg>
                <p style={{ 'position': 'relative', 'fontWeight': 'bold', 'top': '-100px', 'textAlign': 'center', 'fontSize': '60px', 'margin': 0 }}>
                    {Math.ceil(this.state.seconds)}
                </p>

                <p style={{ 'position': 'relative', 'fontWeight': 'bold', 'top': '-115px', 'textAlign': 'center', 'fontSize': '16px', 'margin': 0 }}>
                    seconds
                </p>
            </div>
        );
    }
}

export default Timer;
