import GuessPrompt from './GuessPrompt';
import GuessPost from './GuessPost';
import GuessHold from './GuessHold';
import { sanitize } from '../Util';


function renderSwitch(subState, game, player, error, guess, updateGuess, addGuess) {
    switch(subState) {
        case 'prompt':
            return <GuessPrompt game={game} player={player} addGuess={addGuess} error={error} guess={guess} updateGuess={updateGuess} />
        case 'hold':
            return <GuessHold game={game} player={player} error={error} />
        case 'post':
            return <GuessPost game={game} player={player} error={error} />
        default:
            return <div></div>
    }
}

function Guess({ game, player, error, guess, setGuess, addGuess }) {
    function updateGuess(newValue) {
        setGuess(sanitize(newValue, 16));
    }
    return (
        <div className='centralCol' style={{ 'textAlign': 'left'}}>
            {renderSwitch(game.subState, game, player, error, guess, updateGuess, addGuess)}
        </div>
    );
}

export default Guess;
