
import ScoreTable from './ScoreTable';

function GuessPost({ game, player, error }) {

    return (
        <div>
            <p style={{ 'fontSize': '1.5em' }}>
                {game.players[game.descriptionIndex].name} gave the description
                <span style={{ 'fontWeight': 'bold' }}> {game.descriptionToGuess}</span>
                <br /><br />
                They were describing: <br />
                <span style={{ 'fontSize': '2em', 'fontWeight': 'bold' }}>
                    {game.wordToGuess}
                </span>
            </p>
            <ScoreTable game={game}/>
        </div>
    );
}

export default GuessPost;
