import Timer from '../Timer';

function GuessPromptOwn({ game, player, error }) {

    return (
        <div style={{ 'textAlign': 'center' }}>
            <h1 style={{ 'fontSize': '2em', 'marginTop': '20vh' }}>Players are guessing your description</h1>
            <p>
                (You described {player.prompt.word} as {player.description})
            </p>

            <div style={{ 'textAlign': 'center', 'marginTop': '5vh' }}>
                <Timer endTime={game.latestTimeoutEnd} startTime={game.latestTimeoutStart}></Timer>
            </div>
        </div>
    );
}

export default GuessPromptOwn;
