import Timer from '../Timer';

function DescribeHold({ game, player, error }) {

    return (
        <div style={{ 'textAlign': 'center' }}>
            <h1 style={{ 'fontSize': '3em', 'marginTop': '20vh' }}>Waiting for others</h1>
            <p>
                Your description of {player.prompt.word} was {player.description}
            </p>

            <div style={{ 'textAlign': 'center', 'marginTop': '5vh' }}>
                <Timer endTime={game.latestTimeoutEnd} startTime={game.latestTimeoutStart}></Timer>
            </div>
        </div>
    );
}

export default DescribeHold;
