import GuessPromptOther from './GuessPromptOther';
import GuessPromptOwn from './GuessPromptOwn';

function GuessPrompt({ game, player, error, guess, updateGuess, addGuess }) {

    return (
        game.descriptionIndex === player.index ?
        <GuessPromptOwn game={game} player={player} error={error}/>
        :
        <GuessPromptOther game={game} player={player} addGuess={addGuess} error={error} guess={guess} updateGuess={updateGuess}/>
    );
}

export default GuessPrompt;
