response = finalResponse;
// const game = (response !== null) ? response.game : null;
// const player = (response !== null) ? response.player : null;

function renderSwitch(
  state, game, player,
  description, setDescription,
  addDescriptionError,
  addDescription,
  guess, setGuess,
  addGuessError,
  addGuess,
  beginGameError,
  beginGame,
  resetGameError,
  resetGame
) {
  switch (state) {
    case 'lobby':
      return <Lobby game={game} player={player} beginGame={beginGame} error={beginGameError} />
    case 'describe':
      return <Describe game={game} player={player} addDescription={addDescription} error={addDescriptionError} description={description} setDescription={setDescription} />
    case 'guess':
      return <Guess game={game} player={player} addGuess={addGuess} error={addGuessError} guess={guess} setGuess={setGuess} />
    case 'final':
      return <Final game={game} player={player} resetGame={resetGame} resetGameError={resetGameError} />
    default:
      return <div></div>
  }
}

function Game() {
  const [description, setDescription] = useState('');
  const [guess, setGuess] = useState('');
  const [joinName, setJoinName] = useState('');
  const [joinCode, setJoinCode] = useState('');
  const [createName, setCreateName] = useState('');
  const [createRounds, setCreateRounds] = useState('3');
  const [createError, setCreateError] = useState('');
  const [joinError, setJoinError] = useState('');
  const [addDescriptionError, setAddDescriptionError] = useState('');
  const [addGuessError, setAddGuessError] = useState('');
  const [game, setGame] = useState(null);
  // const [game, setGame] = useState(response.game);
  const [player, setPlayer] = useState(null);
  // const [player, setPlayer] = useState(response.player);
  const [beginGameError, setBeginGameError] = useState('');
  const [resetGameError, setResetGameError] = useState('');
  const [ws, setWs] = useState(null);
  const [isConnected, setIsConnected] = useState(true);


  useEffect(() => {
    const newWs = new WebSocket("wss://vast-sierra-80899.herokuapp.com/");
    setWs(newWs);
    // TODO : parse securley
    newWs.addEventListener("message", e => {
      if (e.data.length > 5000) {
        console.log('Response too large');
        return
      }

      setIsConnected(true);

      const response = JSON.parse(e.data);

      if ('player' in response) {
        const newPlayer = {};
        let isValidPlayer = true;
        let requiredPlayerFields = ['id', 'name', 'isHost', 'index'];
        for (let field of requiredPlayerFields) {
          if (!(field in response.player)) {
            isValidPlayer = false;
          }
        }
        if (isValidPlayer) {
          newPlayer.id = sanitize(response.player.id, 16);
          newPlayer.name = sanitize(response.player.name, 16);
          newPlayer.isHost = response.player.isHost;
          newPlayer.index = parseInt(response.player.index);
          if ('description' in response.player) {
            newPlayer.description = sanitize(response.player.description, 16);
          }
          if ('guess' in response.player) {
            newPlayer.guess = sanitize(response.player.guess, 16);
          }
          if ('prompt' in response.player) {
            if (response.player.prompt === null) {
              newPlayer.prompt = null;
            }
            else if (('word' in response.player.prompt) && ('cat' in response.player.prompt)) {
              newPlayer.prompt = {
                word: sanitize(response.player.prompt.word, 16),
                cat: sanitize(response.player.prompt.cat, 16),
              }
            }
          }
          setPlayer(newPlayer);
        }
      }

      if ('game' in response) {
        const newGame = {};
        let isValidGame = true;
        let requiredGameFields = ['id', 'state', 'subState', 'round'];
        for (let field of requiredGameFields) {
          if (!(field in response.game)) {
            isValidGame = false;
          }
        }
        if (isValidGame) {
          newGame.id = sanitize(response.game.id, 6);
          newGame.state = sanitize(response.game.state, 16);
          newGame.subState = sanitize(response.game.subState, 16);
          newGame.round = parseInt(response.game.round);
          if ('descriptionToGuess' in response.game) {
            newGame.descriptionToGuess = sanitize(response.game.descriptionToGuess, 16);
          }
          if ('catToGuess' in response.game) {
            newGame.catToGuess = sanitize(response.game.catToGuess, 16);
          }
          if ('wordToGuess' in response.game) {
            newGame.wordToGuess = sanitize(response.game.wordToGuess, 16);
          }
          if ('descriptionIndex' in response.game) {
            newGame.descriptionIndex = parseInt(response.game.descriptionIndex);
          }
          if ('latestTimeoutStart' in response.game) {
            newGame.latestTimeoutStart = parseInt(response.game.latestTimeoutStart);
          }
          if ('latestTimeoutEnd' in response.game) {
            newGame.latestTimeoutEnd = parseInt(response.game.latestTimeoutEnd);
          }
          if (('players' in response.game) && Array.isArray(response.game.players)) {
            newGame.players = [];
            for (let gamePlayer of response.game.players) {
              let newGamePlayer = {}
              if ('name' in gamePlayer) {
                newGamePlayer.name = sanitize(gamePlayer.name, 16);
              }
              if ('guess' in gamePlayer) {
                newGamePlayer.guess = sanitize(gamePlayer.guess, 16);
              }
              if ('score' in gamePlayer) {
                newGamePlayer.score = parseInt(gamePlayer.score);
              }
              if ('isHost' in gamePlayer) {
                newGamePlayer.isHost = gamePlayer.isHost;
              }
              newGame.players.push(newGamePlayer);
            }
          }
          setGame(newGame);
          if ('state' in newGame) {
            if (newGame.state !== 'guess') {
              setGuess('');
            }
            if (newGame.state !== 'describe') {
              setDescription('');
            }
          }
        } else {
          console.log('invalid game');
        }
      }
    });
    newWs.onclose = (e => {
      setIsConnected(false);
    });
  }, []);


  function createGame() {
    setCreateError('');
    const name = sanitize(createName, 16);
    if ((!name) || (name !== createName)) {
      setCreateError('Invalid name');
      return
    }
    const requestObject = {
      action: 'newGame',
      playerName: name,
      rounds: parseInt(createRounds)
    }
    if ((ws !== null) && (ws.readyState === ws.OPEN)) {
      ws.send(JSON.stringify(requestObject));
    } else {
      setCreateError('Unable to create game');
    }
  }

  function beginGame() {
    setBeginGameError('');
    const requestObject = {
      action: 'begin',
      playerId: player.id,
      gameId: game.id,
    }
    if ((ws !== null) && (ws.readyState === ws.OPEN)) {
      ws.send(JSON.stringify(requestObject));
    } else {
      setBeginGameError('Unable to begin game');
    }
  }

  function resetGame() {
    setResetGameError('');
    const requestObject = {
      action: 'reset',
      playerId: player.id,
      gameId: game.id,
    }
    if ((ws !== null) && (ws.readyState === ws.OPEN)) {
      ws.send(JSON.stringify(requestObject));
    } else {
      setResetGameError('Unable to reset game');
    }
  }

  function joinGame() {
    setJoinError('');
    const name = sanitize(joinName, 16);
    if ((!name) || (name !== joinName)) {
      setJoinError('Invalid name');
      return
    }
    const code = sanitize(joinCode, 6);
    if ((!code) || (code !== joinCode) || (code.length !== 6)) {
      setJoinError('Invalid code');
      return
    }
    const requestObject = {
      action: 'joinGame',
      playerName: name,
      gameId: code,
    }
    if ((ws !== null) && (ws.readyState === ws.OPEN)) {
      ws.send(JSON.stringify(requestObject));
    } else {
      setJoinError('Unable to join game');
    }
  }

  function addDescription() {
    setAddDescriptionError('');
    const descriptionToSend = sanitize(description, 16);
    if ((!descriptionToSend) || (descriptionToSend !== description)) {
      setAddDescriptionError('Invalid description');
      return
    }
    const requestObject = {
      action: 'addDescription',
      playerId: player.id,
      gameId: game.id,
      description: descriptionToSend
    }
    if ((ws !== null) && (ws.readyState === ws.OPEN)) {
      ws.send(JSON.stringify(requestObject));
    } else {
      setAddDescriptionError('Unable to send description');
    }
  }

  function addGuess() {
    setAddGuessError('');
    const guessToSend = sanitize(guess, 16);
    if ((!guessToSend) || (guessToSend !== guess)) {
      setAddGuessError('Invalid guess');
      return
    }
    const requestObject = {
      action: 'addGuess',
      playerId: player.id,
      gameId: game.id,
      guess: guessToSend
    }

    if ((ws !== null) && (ws.readyState === ws.OPEN)) {
      ws.send(JSON.stringify(requestObject));
    } else {
      setAddGuessError('Unable to send guess');
    }
  }

  return (
    <div className="App">
      <Header text={((game !== null) && (game.round > 0) && (game.state !== 'final')) ? ('Round ' + game.round) : 'Tiptoe Type'} />
      {
        isConnected ? (
          ((typeof (game) !== 'undefined') && (game !== null) && ('state' in game))
            ?
            renderSwitch(
              game.state, game, player,
              description, setDescription,
              addDescriptionError,
              addDescription,
              guess, setGuess,
              addGuessError,
              addGuess,
              beginGameError,
              beginGame,
              resetGameError,
              resetGame
            )
            :
            <Home
              joinName={joinName}
              setJoinName={setJoinName}
              joinCode={joinCode}
              setJoinCode={setJoinCode}
              createName={createName}
              setCreateName={setCreateName}
              createRounds={createRounds}
              setCreateRounds={setCreateRounds}
              joinGame={joinGame}
              createGame={createGame}
              joinError={joinError}
              createError={createError}
            />
        ) : <Disconnected />
      }
    </div>
  );
}

export default Game;