const allowedCharacters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789 ';

function extractAllowedCharacters(input) {
    return JSON.stringify(input).split('').filter(v => allowedCharacters.includes(v)).join('');
}

function sanitize(input, maxLength) {
    return (input === null ? null : extractAllowedCharacters(input).slice(0, maxLength))
}

export {sanitize}
