import tiptoe from '../tiptoe.png';
import { sanitize } from '../Util';

function Home({ joinName, setJoinName, joinCode, setJoinCode,
    createName, setCreateName, createRounds, setCreateRounds, joinGame, createGame, joinError, createError }) {
    return (
        <div className='centralCol'>
            <img src={tiptoe} alt="logo" style={{ 'width': '25vh', 'marginTop': '5vh' }} />
            <div style={{ 'marginTop': '5vh' }}>
                <div className='homeInputCol' style={{ 'borderRight': 'solid 2px grey' }}>
                    <h3 style={{ 'textAlign': 'left' }}>Join Game</h3>
                    <div className='labelledInputContainer'>
                        <div className='labelledInputLabelContainer'>
                            <label htmlFor='joinName' >Name</label>
                        </div>
                        <input
                            className='labelledInputInput'
                            name='joinName'
                            type='text'
                            value={joinName}
                            onChange={e => setJoinName(sanitize(e.target.value, 16))}
                            placeholder='Name'
                        />
                    </div>
                    <div className='labelledInputContainer'>
                        <div className='labelledInputLabelContainer'>
                            <label htmlFor='joinCode' >Code</label>
                        </div>
                        <input
                            className='labelledInputInput'
                            name='joinCode'
                            type='text'
                            value={joinCode}
                            onChange={e => setJoinCode(sanitize(e.target.value, 6))}
                            placeholder='Code'
                        />
                    </div>
                    <div className='labelledInputContainer'>
                        <input onClick={e => joinGame()} type='button' value='Join' />
                    </div>
                </div>
                <div className='homeInputCol'>
                    <h3 style={{ 'textAlign': 'left' }}>Create Game</h3>
                    <div className='labelledInputContainer'>
                        <div className='labelledInputLabelContainer'>
                            <label htmlFor='createName' >Name</label>
                        </div>
                        <input
                            className='labelledInputInput'
                            name='createName'
                            type='text'
                            value={createName}
                            onChange={e => setCreateName(sanitize(e.target.value, 16))}
                            placeholder='Name'
                        />
                    </div>
                    <div className='labelledInputContainer'>
                        <div className='labelledInputLabelContainer'>
                            <label htmlFor='createRounds' >Rounds</label>
                        </div>
                        <select value={createRounds} onChange={e => setCreateRounds(e.target.value)} name='createRounds'>
                            <option value="1">1</option>
                            <option value="3">3</option>
                            <option value="5">5</option>
                        </select>
                    </div>
                    <div className='labelledInputContainer'>
                        <input onClick={e => createGame()} type='button' value='Create' />
                    </div>
                </div>
                <div style={{ 'display': 'block', 'height': '4em' }}>
                    <div className='homeInputCol' style={{ 'borderRight': 'solid 2px rgba(0,0,0,0)' }}>
                        <div className='error' style={{'display': 'inline-block', 'verticalAlign': 'top', 'marginTop': '10px', 'visibility': (joinError === '' ? 'hidden' : 'visible') }}>
                            Error: {joinError}
                        </div>
                    </div>
                    <div className='homeInputCol'>
                        <div className='error' style={{ 'display': 'inline-block', 'verticalAlign': 'top', 'marginTop': '10px', 'visibility': (createError === '' ? 'hidden' : 'visible') }}>
                            Error: {createError}
                        </div>
                    </div>
                </div>

            </div>

        </div>
    );
}

export default Home;
